import React from 'react'
import css from './Main.module.scss'
import Logo from '../../assets/Logo.svg'
import Timer from './timer/Timer'
import Picture from './picture/Picture'

const Main = () => {
    return (
        <div className={css.main}>
            <div className={css.main__header}>
                <div className={css.main__logo}>
                    <img src={Logo} alt='' />
                    <p><span></span></p>
                </div>
                <button></button>
            </div>
            <div className={css.main__container}>
                <div className={css.main__picture}>
                    <Picture />
                </div>
                <div className={css.main__info}>
                    <div className={css.main__infoWrapper}>
                        <p className={css.main__headlineTop}>  </p>
                        <h1>   </h1>
                        <p className={css.main__headlineMiddle}>             </p>
                        <div className={css.main__headlineBottom}>
                            <button></button>
                            <Timer />
                        </div>
                    </div>
                </div>
            </div>
            <div className={css.main__description}>
                <p> <span></span>      ,     
                    <span>  ! </span>
                    ,    .         
                    <span>   .</span></p>
            </div>
        </div>
    )
}

export default Main